.contact-page {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #FCD116 0%, #CE1126 100%);
  padding: 2rem;
}

.contact-container {
  display: flex;
  background-color: white;
  border-radius: 15px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
  max-width: 1100px;
  width: 100%;
}

.contact-info {
  flex: 1;
  background-color: #003893;
  color: white;
  padding: 3rem;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
}

.contact-info h1 {
  margin-bottom: 1rem;
  font-size: 2.5rem;
}

.contact-details {
  margin-top: 2rem;
}

.contact-item {
  display: flex;
  align-items: center;
  margin-bottom: 1rem;
}

.contact-item i {
  margin-right: 1rem;
}

.contact-form {
  flex: 1;
  padding: 3rem;
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: bold;
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 1rem;
}

.submit-btn {
  width: 100%;
  padding: 1rem;
  background-color: #003893;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.submit-btn:hover {
  background-color: #002060;
}

@media (max-width: 768px) {
  .contact-container {
    flex-direction: column;
  }

  .contact-info, .contact-form {
    border-radius: 0;
  }

  .contact-info {
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
  }

  .contact-form {
    border-bottom-left-radius: 15px;
    border-bottom-right-radius: 15px;
  }
}